:root {
  --fontRoboto: 'Roboto', sans-serif;
  --fontMontserrat: 'Montserrat', sans-serif;
  --fontOswald: 'Oswald', sans-serif;
}

.hero-container {
  /*
  background: url('https://ik.imagekit.io/y30wnrs9bpc/images/img-home_7e3JWFQ1N-i.jpg?updatedAt=1632152386501');
  */
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}
video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

.hero-container > h1 {
  text-align: center;
  color: #fff;
  font-size: 100px;
  margin-top: 20px;
  font-family: var(--fontMontserrat);
}

.hero-container > p {
  margin-top: 8px;
  color: #fff;
  font-size: 32px;
  font-family: var(--fontMontserrat);
}
.hero-btns {
  margin-top: 32px;
}

.hero-btns .btn {
  margin: 6px;
}
.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .hero-container > h1 {
    font-size: 70px;
    margin-top: 40px;
  }
}

@media screen and (max-width: 768px) {
  .hero-container > h1 {
    font-size: 50px;
    margin-top: 20px;
  }
  .hero-container > p {
    font-size: 30px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }
  .btn {
    width: 100%;
  }
}
